import React, { useRef } from 'react';
import Slider from 'react-slick';
import PropTypes from 'prop-types';
import ImageWrapper from '../components/image-wrapper';
import VideoWrapper from '../components/video-wrapper';
import SlickText from '../components/slick-text';
import { SLICK_DEFAULT_SETTINGS } from '../constants';
import { map, merge } from 'lodash';

const GallerySlider = ({ settings, sliderData }) => {
    // const { onChange, imageUrlsList, settings } = props;
    // const [index, setIndex] = useState(0);
    const sliderRef = useRef(null);
    const playerRef = useRef(null);

    const onSliderChange = newIndex => {
        // setClickable(true);
        // setIndex(newIndex);
        // onChange(newIndex);
        console.log(sliderRef);
        console.log(playerRef);
        console.log(`New index val ${newIndex}`);
        // playerRef.current && playerRef.current.player.fullscreen.enter();
        // if (sliderRef.innerSlider) {
        //     sliderRef.innerSlider.list.setAttribute('tabindex', 0);
        //     sliderRef.innerSlider.list.focus();
        // }
        playerRef.current && playerRef.current.player.pause();
    };

    const onClickHandle = index => {
        console.log(`Slider item has been clicked ${index}`);
    };

    const beforeChangeSlide = (oldIndex, newIndex) => {
        // setClickable(false);
        // console.log(sliderRef);
        // console.log(playerRef);
        // playerRef.curent.player && playerRef.curent.player.pause();
        // playerRef.current && playerRef.current.player.fullscreen.exit();
        console.log('Before change called ', oldIndex, newIndex);
    };
    const methodDefaults = {
        afterChange: onSliderChange,
        beforeChange: beforeChangeSlide,
    };
    const combinedSettings = merge({}, settings, SLICK_DEFAULT_SETTINGS, methodDefaults);

    return (
        <div className="media-slider">
            <Slider ref={sliderRef} {...combinedSettings}>
                {sliderData &&
                    sliderData.length &&
                    map(sliderData, (item, index) =>
                        item.type === 'image' ? (
                            <React.Fragment key={index}>
                                <ImageWrapper
                                    item={item}
                                    index={index}
                                    onClickHandler={onClickHandle}
                                    count={sliderData.length}
                                />
                                <SlickText key={item.title} item={item} index={index} totalCount={sliderData.length} />
                            </React.Fragment>
                        ) : (
                            <React.Fragment key={index}>
                                <VideoWrapper
                                    item={item}
                                    index={index}
                                    onClickHandler={onClickHandle}
                                    count={sliderData.length}
                                />
                                <SlickText key={item.title} item={item} index={index} totalCount={sliderData.length} />
                            </React.Fragment>
                        )
                    )}
            </Slider>
        </div>
    );
};

GallerySlider.propTypes = {
    settings: PropTypes.object,
    sliderData: PropTypes.arrayOf(PropTypes.object),
    onChange: PropTypes.func,
    onClickHandle: PropTypes.func,
};

export default GallerySlider;
