import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import GalleySlider from './components/gallery-slider';
import { find, matchesProperty } from 'lodash';

// property: props.property,
// code: props.code,
class GallerySlider extends PureComponent {
    constructor(props) {
        // const {} = props;
        super(props);
        this.state = {
            sliderData: this.filterSliderData(),
            isCodeFilterAvailable: props.isCodeFilterAvailable,
            isPoprertyFilterAvailable: props.isPoprertyFilterAvailable,
            slideCounter: 0,
        };
    }

    filterSliderData() {
        const { sliderData, isCodeFilterAvailable, isPoprertyFilterAvailable, property, code } = this.props;
        const filterPropertyWithCode = sliderData => {
            const { galleryData } = find(sliderData, matchesProperty('propertyId', property));
            const galleryFiltered = find(galleryData, matchesProperty('galleryCode', code));

            //     if(item.propertyId === property && item.galleryData) {
            //         item.galleryData.filter((galleryItem) => {
            //            return galleryItem.galleryData === code
            //        })
            //    } else {
            //        console.log('error processing data');
            //    }
            // const codeFiltered = propertyFiltered.filter((item) => {
            //     (item.galleryData) && item.galleryData.filter((item) => {
            //         return item.galleryCode === code
            //     })
            // })
            return galleryFiltered;
        };
        const { items } = property && code ? filterPropertyWithCode(sliderData) : [];
        const sortedItems = items.sort((a, b) => Number(a.order) - Number(b.order));

        console.log(sortedItems);

        return sortedItems;
    }

    render() {
        const { sliderData } = this.state;

        return <GalleySlider settings={this.props.settings} sliderData={sliderData} />;
    }
}

GallerySlider.propTypes = {
    code: PropTypes.string,
    isCodeFilterAvailable: PropTypes.bool,
    isPoprertyFilterAvailable: PropTypes.bool,
    property: PropTypes.string,
    settings: PropTypes.object,
    sliderData: PropTypes.arrayOf(PropTypes.object),
};
GallerySlider.defaultProps = {
    isCodeFilterAvailable: true,
    isPoprertyFilterAvailable: true,
    property: '',
    code: '',
};

export default GallerySlider;
